colors {
  /* VERDES */
  color: #96cecc;
  color: #8cb8c9;
  color: #9edbca;
  color: #a0c3cb;
  color: #7db1b1;

  /* PRETOS */
  color: #656d78;
  color: #434a54;
  color: #3c3b3d;
  color: #323133;

  /* WILD */
  color: #e0c341;
  color: #d8334a;
  color: #8067b7;
  color: #48cfad;
  color: #37bc9b;

  /* CIZENTOS */
  color: #b3b3b3;
  color: #808080;
  color: #aab2db;
  color: #ccd1d9;
  color: #f5f7f1;
  color: #e6e9ed;
}

:root {
  /* COLORS */
  --primary-color: #0ba092;
  --secondary-color: #00b09b;
  --bg-primary: #329094;
  --color-white: #f5f5f5;
  /* FONTS */
  --font-main: "Open Sans Condensed";
  --font-extra: "Archivo Narrow";
  --font-wild: "Contrail One";

  /* MEAUSERS */
  --breakpoint-sm: 576px;
  --breakpoint-md: 768px;
  --breakpoint-lg: 992px;
  --breakpoint-xl: 1200px;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--font-main);
  background-color: #ececec;
}

html,
button,
input,
select,
textarea,
.pure-g [class*="pure-u"] {
  font-family: inherit;
}
hr {
  border-color: #3f58b1;
  margin: 0 auto;
  width: 80%;
}
a {
  text-decoration: none;
  color: inherit;
  outline: none;
}

a:hover {
  color: inherit;
  outline: none;
  cursor: pointer;
  text-decoration: none;
}

.layout {
  width: 100%;
  position: relative;
  min-height: 100vh;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-wrap: nowrap;
      flex-wrap: nowrap;
}

.c4f-default-container {
  -ms-flex: 1 1;
      flex: 1 1;
  min-height: 600px;
}

/* TITLES */
.title-h1 {
  font-weight: 700;
  font-size: 4rem;
  font-family: var(--font-main), sans-serif;
  color: var(--secondary-color);
  padding: 0 10%;
}

.c4f-icon {
  text-shadow: 0 1px 0 rgba(0, 0, 0, 0.1);
  font-size: 15px;
  color: var(--color-white);
}

.cf-icon {
  text-shadow: 0 1px 0 rgba(0, 0, 0, 0.1);
  font-size: 15px;
  color: #cacaca;
}

.cf-container-btn-option:hover > span {
  color: #333;
  cursor: pointer;
  -webkit-transition: color 100ms ease-in;
  -o-transition: color 100ms ease-in;
  transition: color 100ms ease-in;
}

.cf-button-icon {
  background-color: transparent;
  border: 1px solid #e1e1e1;
  border-radius: 2px;
  padding: 5px;
}
.cf-button-icon > span {
  width: 23px;
}
.cf-button-icon:hover {
  background-color: #f1f1f1;
  border-color: #3f58b1;
}

.cf-button-icon.exit {
  margin-right: 10px;
}

.cf-button-icon.exit > span {
  color: #e86969;
}

.cf-button-icon.save > span {
  color: #17b565;
}

.cf-info-field {
  display: block;
  text-align: left;
  padding-bottom: 10px;
}
.cf-info-field-title {
  text-transform: uppercase;
  font-size: 1rem;
  color: #3f58b1;
}

.cf-info-field-text {
  font-weight: 300;
}



