.c4f-navbar{
    width: 100%;
    height: 75px;
    position: relative;
    z-index: 100;
    background-color: var(--bg-primary);
}
    .c4f-navbar-wrapper{
        margin-left: 2%;
        height: 100%;
        display: -ms-flexbox;
        display: flex;
    }

        .c4f-navbar-logo{
            -ms-flex-item-align: center;
                align-self: center;
            height: 70px;
            width: 160px;
            background-image: url("../../images/logo-chair4future-5.png");
            background-position: center;
            background-size: contain;
            background-repeat: no-repeat;
        }

              
        .c4f-navbar-content{
            -ms-flex: 1 1;
                flex: 1 1;
            display: -ms-flexbox;
            display: flex;
            -ms-flex-align: center;
                align-items: center;
            -ms-flex-pack: center;
                justify-content: center;
        }

        /*NAVBAR FORM*/
        .no-auth-content{
            display: -ms-flexbox;
            display: flex;
            -ms-flex-direction: row;
                flex-direction: row;
            -ms-flex-wrap: nowrap;
                flex-wrap: nowrap;
            -ms-flex-align: center;
                align-items: center;
        }

        .c4f-navbar-form{
            display: -ms-flexbox;
            display: flex;
            -ms-flex-direction: column;
                flex-direction: column;
            -ms-flex-wrap: nowrap;
                flex-wrap: nowrap;
            -ms-flex: 1 1;
                flex: 1 1;
        }

        .c4f-navbar-inputs{
            display: -ms-flexbox;
            display: flex;
            -ms-flex-direction: row;
                flex-direction: row;
        }
        .c4f-nav-item{
            margin: 0 10px;
            color: var(--color-white);
        }
        .no-password{
            display: inline-block;
            font-size: 0.8rem;
            font-family: 'Nunito Sans', sans-serif;
        }

        /*END OF NAVBAR FORM*/

        /*NAVBAR AUTH CONTENT*/
        .auth-content{
            display: -ms-flexbox;
            display: flex;
            -ms-flex-direction: row;
                flex-direction: row;
            -ms-flex-wrap: nowrap;
                flex-wrap: nowrap;
            -ms-flex-align: center;
                align-items: center;
            height: 100%;
            width: 100%;
        }
            .searchbar{
                justify-self: flex-start;
                font-family: 'Nunito Sans', sans-serif;
                color: var(--color-white);
                border-radius: 3px;
                padding-left: 12px;
                border: none;
                background-color: #59c1b8;
                margin-left: 12%;
            }

            .searchbar:focus{
                outline: none;
                -webkit-box-shadow: 0 1px 8px 0 #29b8ac;
                        box-shadow: 0 1px 8px 0 #29b8ac;
            }

            .c4f-input-searchbar {
                background-color: transparent;
                border: none;
                padding: 13px 20px;
                height: 100%; 
            }

            .c4f-input-searchbar:focus {
                outline: none;
            }

            .c4f-navigation{
                display: -ms-flexbox;
                display: flex;
                -ms-flex-direction: row;
                    flex-direction: row;
                -ms-flex-wrap: nowrap;
                    flex-wrap: nowrap;
                -ms-flex: 1 1;
                    flex: 1 1;
                -ms-flex-align: center;
                    align-items: center;
                -ms-flex-pack: center;
                    justify-content: center;
            }

            .c4f-navigation-link{
                margin: 0 10px;
                padding: 7px 3px;
                color: var(--color-white);
                font-family: 'Nunito Sans', sans-serif;
                font-size: 0.8rem;
                text-transform: uppercase;
                -webkit-transition: color 100ms ease-in;
                -o-transition: color 100ms ease-in;
                transition: color 100ms ease-in;
            }

            .c4f-navigation-link:hover{
                color: #18218c;
                cursor: pointer;
            }

            .c4f-navigation-link.active{
                color: #18218c;
                font-weight: 700;
            }


            .c4f-icons{
                display: -ms-flexbox;
                display: flex;
                -ms-flex-direction: row;
                    flex-direction: row;
                -ms-flex-wrap: nowrap;
                    flex-wrap: nowrap;
                -ms-flex-align: center;
                    align-items: center;
                -ms-flex-pack: center;
                    justify-content: center;
                -ms-flex-preferred-size: 15%;
                    flex-basis: 15%;
            }

            .c4f-nav-icon{
                margin: 14px;
            }
        /*END OF NAVBAR AUTH CONTENT*/




        .c4f-navbar-search{
            -ms-flex: 1 1;
                flex: 1 1;
            display: -ms-flexbox;
            display: flex;
            -ms-flex-align: center;
                align-items: center;
            -ms-flex-pack: center;
                justify-content: center;           
            padding-left: 3%;
        }
            #search-icon{
                color:white;
                margin-top: 5px;
            }
            .c4f-navbar-search-input{
                border: none;
                background-color: transparent;
                padding:8px;
                border-bottom: 1px solid thistle;
                color:white;
                width: 30%;
                -webkit-transition: width 0.5s;
                -o-transition: width 0.5s;
                transition: width 0.5s;
                font:100 13.3333px Arial;
            }

            .c4f-navbar-search-input::-webkit-input-placeholder{
                color:white;
                font: 400 13.3333px Arial;
            }

            .c4f-navbar-search-input:-ms-input-placeholder{
                color:white;
                font: 400 13.3333px Arial;
            }

            .c4f-navbar-search-input::-ms-input-placeholder{
                color:white;
                font: 400 13.3333px Arial;
            }

            .c4f-navbar-search-input::placeholder{
                color:white;
                font: 400 13.3333px Arial;
            }

            .c4f-navbar-search-input:focus{
                outline: none;
                -webkit-transition: width 0.5s;
                -o-transition: width 0.5s;
                transition: width 0.5s;
                border-bottom: 2px solid white;
                width:35%
            }

    
      
        .c4f-grid-navbar{
            width: 70%;
            padding-left: 20%;
        }

        .c4f-navbar-content-input{
            padding: 4px;
            width:100%;
            font:100 13.3333px Arial;
        }

  
        .c4f-navbar-content-login-button{
            background-color:transparent;
            padding:8px;
            -webkit-transition-duration: 0.4s;
                 -o-transition-duration: 0.4s;
                    transition-duration: 0.4s;
            cursor: pointer;
            font:100 13.3333px Arial;
            color:white;
            border-radius:3px;
            border:1px solid white;
            width:125px;
        }

        .c4f-grid-navbar-button{
            background-color: #00b09b;
            -webkit-transition-duration: 0.4s;
                 -o-transition-duration: 0.4s;
                    transition-duration: 0.4s;
            cursor: pointer;
            font:100 14px Arial;
            color:white;
            border: 1px solid #00b09b;
            border-radius:50%;
            
        }

        .c4f-grid-navbar-button:hover{
            background-color: #009688;
            -webkit-box-shadow: 0 12px 16px 0 rgba(0,0,0,0.24), 0 17px 50px 0 rgba(0,0,0,0.19);
                    box-shadow: 0 12px 16px 0 rgba(0,0,0,0.24), 0 17px 50px 0 rgba(0,0,0,0.19)
        }




